.profile-header {
    display: flex;
    align-items: center;
    justify-content: center; /* Center align horizontally */
    margin-bottom: 40px;
    background-color: var(--secondary-color); /* Use white as background color */
    border-radius: 10px;

    @media (max-width: 768px) {
        flex-direction: column;
        align-items: center; /* Center align items vertically */
    }
}

.profile-pic {
    width: 180px;
    height: 180px;
    border-radius: 50%;
    margin-right: 40px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease;

    @media (max-width: 768px) {
        width: 120px;
        height: 120px;
        margin-right: 0;
        margin-bottom: 20px; /* Space between image and text */
    }
}

.profile-header-info {
    @media (max-width: 768px) {
        text-align: center; /* Center align text */
    }
}

.profile-header-info h1 {
    margin: 0;
    font-size: 2.5em;
    color: var(--text-color); /* Dark text color */

    @media (max-width: 768px) {
        font-size: 2em; /* Adjust font size for smaller screens */
    }
}

.profile-header-info h2 {
    margin: 10px 0;
    color: var(--light-text-color); /* Lighter text color */
    font-weight: 300;
}

.profile-pic:hover {
    transform: scale(1.05);
}

.profile-header-info p {
    margin: 5px 0;
    color: var(--lightest-text-color); /* Lightest text color */
}

.download-button {
    background-color: var(--primary-color); /* Primary color for button */
    color: var(--secondary-color); /* White text color */
    border: none;
    padding: 10px 20px;
    font-size: 1em;
    cursor: pointer;
    transition: background-color 0.3s ease, box-shadow 0.3s ease;
    margin-top: 10px;
    border-radius: 5px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2); /* Optional shadow for better visibility */
}

/* Adjust hover state to use a darker shade */
.download-button:hover {
    background-color: var(--hover-color); /* Slightly darker shade for hover */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3); /* Enhanced shadow on hover */
    outline: none; /* Remove default outline */
}