/* Assuming these styles are in a CSS file for the language switcher component */

.language-switcher {
    position: fixed;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: var(--secondary-color); /* White background for contrast */
    border-radius: 8px;
    padding: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    width: 150px; /* Fixed width for the container */
    z-index: 9999; /* High z-index to ensure it stays on top */
}

.language-button {
    background-color: var(--primary-color); /* Primary color */
    color: var(--secondary-color); /* White text color */
    border: none;
    border-radius: 4px;
    padding: 10px;
    margin-bottom: 5px;
    cursor: pointer;
    transition: background-color 0.3s, transform 0.2s;
    font-size: 16px;
    width: 100%; /* Makes button width 100% of its container */
    box-sizing: border-box; /* Ensures padding and border are included in the width */
}

.language-button:hover {
    background-color: var(--hover-color); /* Slightly darker shade on hover */
    transform: scale(1.05); /* Slightly increase button size on hover */
}

.language-button:active {
    background-color: darken(var(--primary-color), 20%); /* Even darker shade on click (using SCSS) */
}

@media (max-width: 768px) {
    .language-switcher {
        width: 120px; /* Adjust width for smaller screens */
        right: 10px; /* Less space on smaller screens */
    }

    .language-button {
        font-size: 14px; /* Smaller font size on smaller screens */
    }
}
