:root {
  --primary-color: #0c9ba1;
  --secondary-color: #ffffff; 
  --text-color: #333;
  --link-color: #0c9ba1;
  --hover-color: #009a9a;
  --background-color: #ffffff;
  --card-background: #f9f9f9;
  --card-text-color: #333;
  --button-background: #007bff;
  --button-text: #ffffff;
  --button-hover: #0056b3;
  --section-heading-border: #0c9ba1;
  --scroll-to-top-button-bg: #0c9ba1;
  --scroll-to-top-button-hover: #009a9a;
}

[data-theme='dark'] {
  --primary-color: #1d9c9a;
  --secondary-color: #333;
  --text-color: #eaeaea;
  --link-color: #1d9c9a;
  --hover-color: #159a8b;
  --background-color: #1c1c1c;
  --card-background: #2c2c2c;
  --card-text-color: #eaeaea;
  --button-background: #0c9ba1;
  --button-text: #ffffff;
  --button-hover: #009a9a;
  --section-heading-border: #1d9c9a;
  --scroll-to-top-button-bg: #1d9c9a;
  --scroll-to-top-button-hover: #159a8b;
}
/* Apply global styles */
body {
  background-color: var(--background-color);
  color: var(--text-color);
  font-family: 'Roboto', sans-serif;
}

a {
  color: var(--link-color);
  text-decoration: none;
}

a:hover {
  color: var(--hover-color);
}

html {
  scroll-behavior: smooth;
}
/* fonts.css */
@font-face {
    font-family: 'Roboto';
    src: url('../public/assets/fonts/Roboto-Light.ttf') format('truetype');
    font-weight: 300;
    font-style: normal;
  }
  
  @font-face {
    font-family: 'Roboto';
    src: url('../public/assets/fonts/Roboto-Regular.ttf') format('truetype');
    font-weight: 400;
    font-style: normal;
  }
  
  @font-face {
    font-family: 'Roboto';
    src: url('../public/assets/fonts/Roboto-Bold.ttf') format('truetype');
    font-weight: 700;
    font-style: normal;
  }

  